import React, { Component } from 'react';
import style from './style.scss';

import titleImg from './title.jpg';
import beforeImg from './before.jpg';
import afterImg from './after.jpg';

class TestimonialsSection extends Component {

  constructor(props){
    super(props);
    this.state = {
      active: false
    }
  }

  render() {

    return (
      <div className={`${style.root} ${this.state.active ? style.active : null}`} id="Testimonials">
        <div className={style.title}>
          <img src={titleImg} alt="Testimonials" />
        </div>

        <div className={style.photo}>
          <img src={beforeImg} alt="before" className={style.before} />
          <img src={afterImg} alt="after" className={style.after} />
        </div>
        
        <div className={style.text}>
          <b>Alisa Arslanova, makeup artist</b>
          
          <br/>So this is me around a year ago on the left. My weight is about 230 lbs. I've maintained this super unhealthy weight for about two years and couldn’t really do much about it. I lived and Europe and ate unhealthy, irregular and junk.
          <br/>I think it was around February when I met my amazing trainer- Olga.
          <br/>This day changed my life to the better and believe it or not I hate writing reviews or 
          <span>          
            comments but this is a life miracle
            <br/>This is me on the right today. My weight is 206lbs and this is definitely not the end result. I didn't workout constantly because of my work schedule and chaotic life moments, but this is where we are now. Do I feel better? Yes! Do I want to lose more and gain more myself? Hell Yeah! This is not even a mid point but I am very happy! Olga is a great trainer and a life coach. She is always there for me. she helped me to be present in the moment and most importantly she motivated me to believe in myself! I am thankful for this meeting and after seeing results, I want to get better and better. Hold me accountable guys ~
          </span>
          <button 
            type="button" 
            onClick={ () => { this.setState({active: true}) } }
          >
            Read More
          </button>
        </div>
      </div>
    );
  }
}

export default TestimonialsSection;



