import React, { Component } from 'react';

import Header from 'components/Header';
import Footer from 'components/Footer';
import CarouselSection from './CarouselSection';
import ProductsSection from './ProductsSection';
import EveryDaySection from './EveryDaySection';
import AboutSection from './AboutSection';
import PresentSituationSection from './PresentSituationSection';
import TestimonialsSection from './TestimonialsSection';
import TheOnlySection from './TheOnlySection';
import FuturedVideoSection from './FuturedVideoSection';
import ReleasePotentialSection from './ReleasePotentialSection';

import ConfirmFormDialog from 'components/dialogs/ConfirmForm';

class MainPage extends Component {

  constructor(props) {
    super(props);

    this.state = {
      isConfirmOpen: false,
      params: false
    }
  }
  open = (params = {}) => {
    this.setState({isConfirmOpen: true, params})
  }

  render() {
    const { isConfirmOpen } = this.state;

    return (
      <div>
        <ConfirmFormDialog isOpen={isConfirmOpen} params={this.state.params} />
        <Header />
        <CarouselSection open={this.open}/>
        <ProductsSection />
        <EveryDaySection />
        <AboutSection />
        <PresentSituationSection />
        <TestimonialsSection />
        <TheOnlySection />
        <FuturedVideoSection />
        <ReleasePotentialSection open={this.open} />
        <Footer />
      </div>
    );
  }
}

export default MainPage;



