import React, { Component } from 'react';
import style from './style.scss';

import bgImg from './bg.jpg';

class TheOnlySection extends Component {

  constructor(props){
    super(props)

    this.state = {
      name: '',
      phone: '',
      email: ''
    }
  }

  handleSubmit = (ev) => {
    ev.preventDefault();
    this.props.open({payload: { ...this.state }});
  }

  handleChange = (ev) => {
    const { name, value } = ev.target;
    
    const payload = { [name]: value};
    this.setState(payload);
  }

  render() {

    return (
      <div className={style.root}>
        <img src={bgImg} alt="Release you potential"/>
        <div className={style.form}>
          <form onSubmit={this.handleSubmit}>
            <input type="text" required name="name" placeholder="Name"/>
            <input type="text" required name="phone" placeholder="Phone number"/>
            <input type="text" required name="email" placeholder="Email"/>
            <button type="submit" onClick={this.props.open}>
              Send
            </button>
          </form>
        </div>
        <div className={style.title}>
          Release you potential
        </div>
      </div>
    );
  }
}

export default TheOnlySection;



