import React, { Component } from 'react';
import style from './style.scss';

import bgImg from './bg.jpg';

class PresentSituationSection extends Component {
  render() {

    return (
      <div className={style.root}>
        <img src={bgImg} alt="You present situation is not your final destination"/>
      </div>
    );
  }
}

export default PresentSituationSection;



