import React, { Component } from 'react';
import style from './style.scss';

class Item extends Component {

  constructor(props) {
    super(props);

    this.state = {
      active: false
    }
  }
  render() {

    const { bg, titleOne1, titleOne2, titleTwo1, titleTwo2, btn } = this.props;
    
    return (
      <div className={`${style.root} ${this.active ? 'active' : ''}`}>
        <div className={style.bg} style={{backgroundImage: `url('${bg}')`}} />
        <div className={style.titleOne}>
          {titleOne1}
          <br />{titleOne2}
        </div>
        <div className={style.titleTwo}>
          {titleTwo1}
          <br />{titleTwo2}
        </div>
        <div className={style.action}>
          <button 
            type="button" 
            onClick={btn.action} 
            onMouseEnter={() => {this.setState({active: true})}}
            onMouseLeave={() => {this.setState({active: false})}}
          >
            {btn.text}
          </button>
        </div>
      </div>
    );
  }
}

export default Item;



