import React, { Component } from 'react';
import style from './style.scss';
import logo from './logo.png';

import instagramImg from 'assets/icons/instagram.png';
import twitterImg from 'assets/icons/twitter.png';
import facebookImg from 'assets/icons/facebook.png';

class Footer extends Component {
  render() {
    return (
      <div className={style.root}>
        <a href="/" className={style.logo}>
          <img 
            src={logo}
            alt="logo"
          />
        </a>

        <span>(с) 2018 All rights reserved</span>
        <span className={style.contacts}>
          <a href="https://www.instagram.com/">
            <img src={instagramImg} alt="instagram link" />
          </a>
          <a href="https://twitter.com/">
            <img src={twitterImg} alt="twitter link" />
          </a>
          <a href="https://www.facebook.com/">
            <img src={facebookImg} alt="facebook link" />
          </a>
        </span>
      </div>
    );
  }
}

export default Footer;



