import React, { Component } from 'react';
import style from './style.scss';

import bgImg from './bg.jpg';

class TheOnlySection extends Component {

  render() {

    return (
      <div className={style.root}>
        <img src={bgImg} alt="The only thing standing between you anf your goals, is that ongoing story keep you keep telling youtself, as to why you can`t arhieve it"/>
        <div className={style.title}>
          The only thing standing between you anf your
          <br />goals, is that ongoing story keep you keep telling
          <br />youtself, as to why you can`t arhieve it
        </div>
      </div>
    );
  }
}

export default TheOnlySection;



