import React, { Component } from 'react';

import img1 from './images/1.jpg';
import img2 from './images/2.jpg';
import img3 from './images/3.jpg';

import instagramImg from 'assets/icons/instagram.png';
import twitterImg from 'assets/icons/twitter.png';
import facebookImg from 'assets/icons/facebook.png';

import Slider from 'react-slick';
import style from './style.scss';

class CarouselSection extends Component {
  render() {

    var settings = {
      dots: true,
      dotsClass: style.dots,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      arrows: false,
      customPaging: () => (<button type="button" />)
    }

    return (
      <div className={style.root}>
        <div className={style.motivator}>
          Start your Fitness Journey with me today!
        </div>
        <div className={style.actor}>
          <span>Olga</span> <span>Karavaeva</span>
        </div>
        <div className={style['action-btn']}>
          <button type="button" onClick={this.props.open}>
            Take yout free consultation
          </button>
        </div>
        <div className={style.contacts}>
            <a href="https://www.instagram.com/">
              <img src={instagramImg} alt="instagram link" />
            </a>
            <a href="https://twitter.com/">
              <img src={twitterImg} alt="twitter link" />
            </a>
            <a href="https://www.facebook.com/">
              <img src={facebookImg} alt="facebook link" />
            </a>
        </div>
        <Slider {...settings}>
          {[img1, img2, img3].map((img, idx) => (
            <div key={idx} className={style.slide}>
              <img src={img} alt="Olga Karavaeva" />
            </div>
          ))}
        </Slider>
      </div>
    );
  }
}

export default CarouselSection;



