import React, { Component } from 'react';

import img1 from './images/test1.jpg';
import img2 from './images/test2.jpg';

import style from './style.scss';

import Item from './Item';

class ProductsSection extends Component {

  items = [{
    bg: img1,
    titleOne1: 'ONLINE COACHING',
    titleOne2: 'with Olga Karavayeva',
    titleTwo1: '12 Week Transformation',
    titleTwo2: 'Challege',
    btn: {
      text: 'Apply now',
      action: () => {}
    }
  }, {
    bg: img2,
    titleOne1: 'CHANGE YOR BODY,',
    titleOne2: 'MIND, LIFE',
    titleTwo1: 'Book your personal training',
    titleTwo2: 'session now',
    btn: {
      text: 'Book now',
      action: () => {}
    }
  }]

  render() {

    return (
      <div className={style.root} id="Services" >
        {this.items.map((item, idx) => (
          <Item 
            key={idx}
            {...item}
          />
        ))}
      </div>
    );
  }
}

export default ProductsSection;



