import React, { Component } from 'react';
import style from './style.scss';

import bgImg from './bg.jpg';

class FuturedVideoSection extends Component {
  render() {

    return (
      <div className={style.root} id="Video">
        <img src={bgImg} alt="Futured Video"/>

        <iframe title="Olga Karavaeva" src="https://www.youtube.com/embed/V6oWUSOhKQI?rel=0"></iframe>
      </div>
    );
  }
}

export default FuturedVideoSection;



